#summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.summary-heading {
  width: 100%;
  max-width: 470px;
}

.summary-heading strong {
  font-size: 1rem;
  color: var(--blueColor);
  text-transform: uppercase;
  letter-spacing: 3px;
}

.summary-heading h2 {
  font-size: 2rem;
  line-height: 2.9rem;
  margin: 20px 0;
}

.summary-details {
  width: 100%;
  max-width: 470px;
  border: 1px dashed var(--grayColor);
  padding: 15px 20px;
}

.summary-details p{
  color: var(--grayColor);
  font-size: 1rem;
}

/* Responsiveness */
@media (max-width: 865px) {
  #summary{
    justify-content: center;
    flex-wrap: wrap;
  }

  .summary-heading,
  .summary-details{
    max-width: none;
  }

  .summary-details{
    margin-top: 30px;
  }
}